    alias: "bloodborne-the-board-game",
    descriptionShort: `Bloodborne: The Board Game - настольная приключенческая игра с миниатюрами и модульным полем по мотивам одноимённой видеоигры. В основе игры боевая система на картах и механика колодостроения.`,
    description: `<p>Славящийся своими особыми целительными традициями, величественный город <i><strong>Ярнам</strong></i> утопает под гнетом расползающейся танственной эпидемии, превращающей людей в свирепых чудовищ. Вы - <i><strong>Охотник</strong></i>, и Ваша задача - покончить с растущей угрозой раз и навсегда. Вам предстоит прорезать себе путь сквозь бешеных животных, кроважадных монстров, обезумевших городских жителей и, возможно, нечто более загадачное и непостижимое, приводящее в ужас людской род с самого начала его существования... Сможете ли Вы пережить бесконечно длинную и мрачную ночь и открыть источник этого безумия?</p>\r\n<p>&nbsp;</p>\r\n<p><strong>Bloodborne: The Board Game&nbsp;</strong>представляет из себя приключенческую ролевую игру, в основе которой лежит сюжетная кампания, а игроки, выступающие в роли Охотников, работают сообща против игры, пытаясь раскрыть секреты и тайны внутри Ярнама и вне его пределов. Снаряжённым особым трансформирующимся оружием Охотникам предстоит, передвигаясь по <i>игровому полю</i>, молниеносно принимать решения и адаптироваться к мириаде разнообразных противников, поджидающих забрёдших путников на тёмных улицах города. Изучайте повадки врагов, пользуйтесь их слабостями и действуйте быстро, чтобы выжить в схватке за свою жизнь. Основанная на <i>колодостроительном карточном движке</i> боевая система сводит к минимуму фактор удачи - победа в бою или ужасная смерть будут зависеть исключительно от Ваших действий и от того, как именно Вы подготовитесь к каждому поединку!</p>\r\n<p>&nbsp;</p>\r\n<p>Смерть - не редкость для скитальцев по грязным улочкам Ярнама. Падшие в бою, ослабшие духом Охотники просыпаются во <i><strong>Сне Охотника</strong></i>, чтобы снова и снова возвращаться в тот кошмар, на который обрекла их судьба. Однако пелена сна смывает не только страшные раны - проснувшись, Вы можете обнаружить, что ранее побежденные противники вернулись на свои места и тот ужас, что вы пережили будет повторяться. Время так же не на стороне Охотников - с каждым часом <strong><i>Кровавая Луна</i></strong> поднимается всё выше и выше над городом, распространяя безумие по всем, даже самым потаённым, его уголкам. Вам и Вашим соратникам придётся действовать ещё быстрее, чтобы окончить Охоту до того как Ярнам встретит свой последний час в пучине безысходного ужаса.</p>\r\n<p>&nbsp;</p>\r\n<p>И напоследок - снаряжаясь на <i><strong>Охоту</strong></i>, всегда помните старинное изречение:</p>\r\n<p><i>"Мы были порождены из крови, проливали кровь и умирали в крови. Но нашим глазам еще только предстоит быть открытыми... Бойтесь древней крови."</i></p>\r\n<p>&nbsp;</p>`,
