    title2: "Brass. Бирмингем",
    alias: "Brass. Бирмингем",
    descriptionShort: `«Brass. Бирмингем» — это история о конкурирующих предпринимателях в период английской промышленной революции в 1770—1870 годах. Мидленс — регион в центральной части Англии. Именно здесь находились главные угольные шахты. Промышленная агломерация была сосредоточена вокруг города Бирмингема. За угольные месторождения и дым заводских труб её прозвали Чёрной страной.`,
    description: `<div class="raw_text_output"> <p style="text-align: justify;">«Brass. Бирмингем» — это история о конкурирующих предпринимателях в период английской промышленной революции в 1770—1870 годах. Мидленс — регион в центральной части Англии. Именно здесь находились главные угольные шахты. Промышленная агломерация была сосредоточена вокруг города Бирмингема. За угольные месторождения и дым заводских труб её прозвали Чёрной страной.</p> <p style="text-align: justify;">&nbsp;</p> <p style="text-align: justify;">В основе игры те же правила, что и у «Brass. Ланкашир». Вы будете строить собственную индустриальную империю, налаживая сообщение по водным каналам и железным дорогам, а также сооружая и модернизируя различные предприятия, такие как текстильные фабрики, угольные шахты, литейные заводы, мануфактуры, гончарные мастерские и пивоварни. Все игровые действия выполняются с помощью карт. Игровой процесс более напряжённый, чем в игре-предшественнице: семнадцать фунтов в качестве стартового капитала, уголь и железо стоят дороже. Также в игре появился новый ресурс (пиво) и жетоны коммерсантов. Типы предприятий переработаны: их стало больше, а их параметры разнообразнее. Добавлены карты-джокеры, которые составляют отдельную колоду и дают возможность строить любое предприятие в любом городе.&nbsp;</p> <p style="text-align: justify;">&nbsp;</p> <p style="text-align: justify;">Каждая эпоха состоит из нескольких раундов, количество которых зависит от количества участников в игре. Игрокам доступны следующие действия (два в ход):</p> <ol style="text-align: justify;"> <li>Строительство предприятия. За некоторые необходимо будет заплатить углём или железом.</li> <li>Модернизация собственных предприятий. Игроки сбрасывают предприятия меньших уровней, чтобы открыть доступ к более сильным предприятиям, приносящим больший доход или большее количество победных очков.</li> <li>Развитие путей водного и железнодорожного сообщения от локаций, уже входящих в вашу транспортную сеть, к новым местам.</li> <li>Продажа произведённых товаров. Производственные здания, соединённые сетью дорог с рынком, дают возможность обменять товары на деньги или победные очки.</li> <li>Получение денег в качестве ссуды.</li> </ol> <p style="text-align: justify;">&nbsp;</p> <p style="text-align: justify;">Партия длится две игровые эпохи: эпоху каналов (1770—1830) и эпоху железных дорог (1830—1870). В игре побеждает предприниматель, накопивший больше всех победных очков по завершении эпохи железных дорог.</p> <p style="text-align: justify;">&nbsp;</p> <p style="text-align: justify;">«Brass. Ланкашир» и «Brass. Бирмингем» базируются на одних и тех же механиках, но отличий в правилах достаточно, и обе игры обладают своим темпом и своим набором разных путей к победе. В любом случае оригинальная концепция ссуд как игровой механики, придуманная Мартином Уолессом, в сочетании с логистическими задачами сделали Brass современной классикой.</p> <div id="gtx-trans" style="position: absolute; left: 375px; top: 774.6px;"> <div class="gtx-trans-icon">&nbsp;</div> </div> </div>`,
