    title2: "Descent: Journeys in the Dark (Second Edition)",
    alias: "Descent-Journeys-in-the-Dark-Second-Edition",
    descriptionShort: `Descent: Странствия во тьме (вторая редакция) (Descent: Journeys in the Dark Second Edition) – это второе издание стратегической игры Descent: Journeys in the Dark. Здесь один игрок будет выступать в роли Властелина, а другие будут сообща бороться против него. Количество игроков от 2 до 5.`,
    description: `<p>Второе издание приключений Descent: Journeys in the Dark. События игры происходят в мире Терринот. Во второе издание входят переоформленное модульное игровое поле, 8 новых героев и около 40 монстров девяти видов, более ясные правила, классовая система героев, режим кампании, продвинутая система опыта, позволяющая тратить его на приобретение новых способностей, и многое другое.</p>\r\n<p>&nbsp;</p>\r\n<p>Во втором издании сделана попытка максимально снизить затраты времени на подготовку к игре, обдумывание хода и само приключение. Этому служат упрощённые правила определения видимости цели, более быстрая подготовка только что исследованных локаций, кубики защиты, снижающие количество расчётов результатов боя, и более короткие сценарии с наличием естественных точек, позволяющих делать паузы между игровыми встречами. Вся техническая информация приведена на соответствующих картах, что избавляет от необходимости заглядывать в правила. Изменён подход к действиям игрока, выступающего &laquo;злодеем&raquo;: теперь ему достаточно добирать каждый ход карты и разыгрывать их в любой подходящий момент, осложняя героям жизнь.</p>\r\n<p>&nbsp;</p>\r\n<p>Теперь все классы героев поделены на четыре архетипа: магов, воинов, разведчиков и лекарей. Каждому классу (например, воину, рыцарю и берсерку) соответствует своя колода карт умений, которые можно набирать в качестве стартового &laquo;арсенала&raquo; героя и пополнять во время игры, приобретая за очки набранного опыта.&nbsp;Включённые в коробку сценарии можно играть как отдельно, так и в виде последовательной кампании, составляющей единую сюжетную линию. Сценарии масштабируются в зависимости от числа игроков и адаптируются под успехи или провалы героев, предоставляя им разные ветви развития сюжета.</p>`,
