    description: `Во время отпуска в Египте вы наконец добираетесь до Великих пирамид. Но поднявшись по узким коридорам лабиринта, понимаете, что потеряли остальную часть группы. После нескольких часов блужданий вы оказываетесь в таинственной усыпальнице, и внезапно каменная дверь закрывается прямо за вами! Вы в западне. На полу лежат покрытый песком блокнот и древний вращающийся диск. Сможете ли вы сбежать или будете навечно погребены под камнями?\r\nВ EXIT: The Game – The Pharaoh's Tomb игроки должны использовать командный дух, креативность и дедукцию, чтобы взламывать коды, решать головоломки, собирать предметы и шаг за шагом обретать свободу.`,
