    id: 2188,
    title: "Кворриорс",
    title2: "Quarriors!",
    title3: "Квориорс",
    alias: "quarriors",
    descriptionShort: `Quarriors — игра, сочетающая в себе «колодостроение» и азарт бросков кубиков. Игроки выступают в роли мистических воинов, соревнующихся за признание в глазах императрицы вымышленной страны. На грани всех 130 кубиков, входящих в комплект игры, нанесены разнообразные значки. В начале игры участники получают по одинаковому набору из 12 кубиков, а на стол выкладывается случайный набор кубиков в качестве общего рынка. В свой ход игрок достаёт 6 случайных кубиков из мешка, бросает их и, основываясь на выпавших гранях, призывает на стол существ и творит заклинания (они представлены картами и требуют для вызова или сотворения определённой комбинации значков на кубиках), а также приобретает кубики с общего рынка, добавляя их в свой мешок. Помимо разнообразных игровых эффектов карты существ и заклинаний приносят необходимые для победы очки.`,
    description: `<p>Quarriors &mdash; игра, сочетающая в себе &laquo;колодостроение&raquo; и азарт бросков кубиков. Игроки выступают в роли мистических воинов, соревнующихся за признание в глазах императрицы вымышленной страны. На грани всех 130 кубиков, входящих в комплект игры, нанесены разнообразные значки. В начале игры участники получают по одинаковому набору из 12 кубиков, а на стол выкладывается случайный набор кубиков в качестве общего рынка. В свой ход игрок достаёт 6 случайных кубиков из мешка, бросает их и, основываясь на выпавших гранях, призывает на стол существ и творит заклинания (они представлены картами и требуют для вызова или сотворения определённой комбинации значков на кубиках), а также приобретает кубики с общего рынка, добавляя их в свой мешок. Помимо разнообразных игровых эффектов карты существ и заклинаний приносят необходимые для победы очки.</p>`,
