    title: "Максимум Апокалипсис",
    title2: "Maximum Apocalypse",
    alias: "Maximum-Apocalypse",
    descriptionShort: `Максимум Апокалипсис - кооперативное настольное приключение, в котором самым разным героям предстоит столкнуться лицом к лицу с концом света.`,
    description: `<p>&nbsp;</p>\r\n<p><i><strong>Максимум Апокалипсис</strong></i> &ndash; это уникальное постапокалиптическое приключение. Что может быть лучше, чем самому выбрать апокалипсис, уничтоживший ваш мир, и героя, которым вы станете, и общую цель для всей команды выживших? Вам есть где разгуляться в своей тяге к разрушению. Зомби? Роботы? Мутанты? Или даже гигантские жуки? От вашего выбора зависит то, с какими противниками на просторах мертвых пустошей вам предстоит столкнуться, какие задания нужно будет выполнить и по каким локациям вы будете путешествовать. Одолейте всех врагов, выполните миссию, заправьте свой фургон &ndash; и отправляйтесь навстречу новым приключениям!</p>\r\n<p>&nbsp;</p>`,
