    alias: "Munchkin-9-Jurassic-Snark",
    descriptionShort: `<p>Ну что, доигрались, манчкины? Может, хотя бы теперь вы перестанете пренебрегать наукой и законами физики? Да ну, бред какой-то! Ведь добравшись до магии и высоких технологий, манчкины отправили себя прямиком в доисторический мир юрского периода, где полно новых сокровищ и приключений!</p><p>Держитесь подальше от извергающихся вулканов, седлайте различных птеро-скакунов, обязательно примерьте новую примитивную, но эффективную экипировку (сейчас в моде камни и кости), а также побеждайте новых свирепых и саблезубых монстров! Шутки с пространственно-временным континуумом плохи, и просто ужасны, когда в это дело лезут манчкины! Постарайтесь не задеть бабочку, а то, кто знает, к чему это приведёт в будущем?..</p>`,
