    title2: "Экосистема",
    alias: "Экосистема",
    descriptionShort: `<div class="desc-text" id="desc"> <p> Совокупность живых организмов, их среды обитания и их тесная взаимосвязь называется экосистема. А ещё "Экосистема" – это тесная взаимосвязь карт на вашем столе, которые при грамотном сочетании принесут вам много победных очков! Эта карточная игра с механикой драфта, в которой вам предстоит создавать собственную экосистему, как можно догадаться из названия.</p> <p> Правила этой игры довольны просты, объясняются минут за 5, а запоминаются уже после первой партии: каждый игрок получает по 10 карт, выбирает из полученной руки одну карту и кладёт перед собой взакрытую. Как только все игроки определятся с картой, они открывают их и выкладывают перед собой – в свою экосистему: она представляет перед собой поле перед игроком размером 5 на 4 карты. После этого игроки передают карты по часовой стрелке. Когда карты в руке заканчиваются, раздаются ещё по 10 карт, и игра продолжается, но с одним отличием: теперь карты передаются игроку против часовой стрелки. Когда и эти 10 карт заканчиваются, начинается этап начисления и подсчёта очков. Тут всё в точности так, как настоящей экосистеме: пчёлы предпочитают цветы, лисы прячутся от волков, а те в свою очередь предпочитают собираться в стаи. Одна и та же карта принесёт вам разное количество очков в зависимости от того, куда вы её разместите.</p> <p> Приятные, красочные иллюстрации дикой природы и простые правила игры делают "Экосистему" хорошей семейной игрой, которая привлечёт внимание игроков любого возраста.</p> </div>`,
